body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Mandelbrot {
  height: 100%;
  width: 100%;
  position: fixed;
}

.mandelTile {
  opacity: 1;
}

.mapButton {
  background: rgba(60, 60, 60, .7);
}
.mapButton:hover {
  cursor: pointer;
  background: rgba(10, 10, 10, .95);
}

.link {
  color: #8470ff !important;
}



